
import { ImageSize, Item, ItemMode, Price } from "@/interface";
import { convertFlickrImage } from "@/mixins/generalMixin";
import { defineComponent } from "vue";
import ActionButtonComponent from "../components/ActionButtonComponent.vue";

export default defineComponent({
  props: ["systemData", "banners", "categories"],
  emits: ["showItemDetail", "updateItemPriceToOrder", "removeOrderListItem"],
  mounted() {
    window.document.title = `${this.systemData.companyName} - Menu Panda`;
  },
  components: {
    ActionButtonComponent,
  },
  data() {
    return {
      ImageSize: ImageSize,
      itemMode: ItemMode,
    };
  },
  methods: {
    toPages(items: Item[]) {
      let results: Item[][] = [];
      let slot = null;
      for (let i = 0; i < items.length; i++) {
        if (i % 3 === 0) {
          results.push([]);
          slot = Math.floor(i / 3);
        }
        results[slot!].push(items[i]);
      }
      return results;
    },
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    updateItemPriceToOrder(value: { itemPrice: Price; qty: number }) {
      this.$emit("updateItemPriceToOrder", value);
    },
    removeOrderListItem(id: number) {
      this.$emit("removeOrderListItem", id);
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
});
