export interface FetchData {
    name: string
    datas: any[]
}

export interface Category {
    id: number
    name: string
    items: Item[]
}

export interface Banner {
    id: number
    image: string
    description: string
    itemCode: string
    item?: Item
}

export interface System {
    id: number
    companyName: string
    companyAddress: string
    companyContact: string
    companyDescription: string
    serviceCharge10Percent: boolean
    tax6Percent: boolean
}

export interface Item {
    id: number
    categoryName: string
    image: string
    code: string
    name: string
    description: string
    note: string
    mode: ItemMode
    addOn?: string // from sheet
    addOnList: AddOn[]
    prices: Price[]
    added: number
    orderHistoryItemPrices: Price[]
    orderListItemPrices: Price[]
}

export interface Price {
    name: string
    added: number
    price: number
    sumPrice: number
    itemInfo: Item
}

export interface AddOn {
    id: number
    name: string
    types?: string // from sheet
    prices?: string // from sheet
    sumPrice: number
    list: AddOnItem[]
    noCharges: boolean
    maximumSelect: number
    defaultSelect?: number
    allowAddMore: boolean
}

export interface AddOnItem {
    name: string
    price: number
    added: number
}


export interface Order {
    id: number
    orderNo?: number
    status: OrderStatus
    tableNo: number
    itemPrices: string | Price[]
    amount: number
    createdDate: string
    deviceId: number
    note: string
}

export interface LSCustomerInfo {
    deviceId: number
}

export interface Sheet {
    name: string
    datas: any[]
}

export enum OrderStatus {
    waiting = 'waiting',
    queueing = 'queueing',
    preparing = 'preparing',
    completed = 'completed',
    paid = 'paid',
    cancelled = 'cancelled',
}

export enum CRUD {
    create = 'create',
    createWithInsert = 'createWithInsert',
    reading = 'reading',
    update = 'update',
    delete = 'delete',
}

export enum PostOrUpdate {
    post = 'post',
    update = 'update',
}

export enum ImageSize {
    'w75' = 's',
    'w100' = 't',
    'w150' = 'q',
    'w240' = 'm',
    'w320' = 'n',
    'w400' = 'w',
    'default' = '',
    'w640' = 'z',
    'w800' = 'c',
    'w1024' = 'b',
    'w1600' = 'h',
}

export enum ItemMode {
    'addQty' = 'add quantity only',
    'addSize' = 'add size',
    'addOption' = 'add multiple options',
}



