
import { defineComponent, ref, getCurrentInstance } from "vue";
import { allSheetJsonToObject, jsonToObject } from "./mixins/forLoopMixin";
import {
  System,
  Banner,
  Category,
  LSCustomerInfo,
  CRUD,
  OrderStatus,
  Order,
  PostOrUpdate,
  Price,
  AddOnItem,
  AddOn,
  FetchData,
  Item,
} from "@/interface";
import ItemDetailComponent from "./components/ItemDetailComponent.vue";
import FooterComponent from "./components/FooterComponent.vue";
import OrderListModalComponent from "./components/OrderListModalComponent.vue";
import AlertModalComponent from "./components/AlertModalComponent.vue";
import OrderHistoryModalComponent from "./components/OrderHistoryModalComponent.vue";
import {
  getCustomerInfoFromLS,
  getOrderListFromLS,
  removeOrderListFromLS,
  setCustomerInfoToLS,
  setOrderListToLS,
} from "./mixins/localStorageMixin";
import { fetchPost, fetchPostByType } from "./mixins/fetchMixin";
import { arrayFind, arraySum } from "./mixins/generalMixin";

export default defineComponent({
  components: {
    ItemDetailComponent,
    FooterComponent,
    OrderListModalComponent,
    AlertModalComponent,
    OrderHistoryModalComponent,
  },
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      dataUrl: proxy.$getDataUrl,
      orderUrl: proxy.$getOrderUrl,
      fetchCode: proxy.$fetchCode,
    });

    let categories = ref<Category[]>([]);
    let items = ref<Item[]>([]);
    let banners = ref<Banner[]>([
      { id: 1, image: "", description: "", itemCode: "" },
    ]);
    let systemData = ref<System>({
      id: 1,
      companyName: "...",
      companyAddress: "",
      companyContact: "",
      companyDescription: "",
      serviceCharge10Percent: false,
      tax6Percent: false,
    });
    let orderHistories = ref<Order[]>([]);
    let orderList = ref<Price[]>([]);
    let customerInfo = ref<LSCustomerInfo>();
    let isLoading = ref<boolean>(true);
    let fetchAllSheets = fetch(
      `${proxyData.value.dataUrl}tqx=out:json&sheet=Datas`
    );
    let fetchOrderHistories;

    if (getCustomerInfoFromLS()) {
      const dataCustomerInfo = getCustomerInfoFromLS();
      const url =
        proxyData.value.orderUrl +
        "sheet=Orders&tq=" +
        encodeURIComponent(
          `select * where K = ${dataCustomerInfo!.deviceId} and (C = "${
            OrderStatus.waiting
          }" or C = "${OrderStatus.queueing}" or C = "${
            OrderStatus.preparing
          }" or C = "${OrderStatus.completed}")`
        );
      fetchOrderHistories = fetch(url);
      customerInfo.value = dataCustomerInfo!;
    } else {
      setCustomerInfoToLS({ deviceId: Date.now() });
    }

    Promise.all([fetchAllSheets, fetchOrderHistories]).then((res) => {
      res[0].text().then((rep) => {
        const allSheets = allSheetJsonToObject(rep);
        let dataCategories = arrayFind<FetchData>(
          allSheets,
          "name",
          "Categories"
        )!.datas;
        let dataAddOnList = arrayFind<FetchData>(
          allSheets,
          "name",
          "AddOnList"
        )!.datas;
        let dataItems = arrayFind<FetchData>(allSheets, "name", "Items")!.datas;
        let dataBanners = arrayFind<FetchData>(
          allSheets,
          "name",
          "Banners"
        )!.datas;
        let dataSystem = arrayFind<FetchData>(allSheets, "name", "System")!
          .datas[0];

        // #region system
        window.document.title = `${systemData.value.companyName} - Menu Panda`;
        // #endregion

        // #region addOnList
        dataAddOnList = dataAddOnList.map((addOn: AddOn) => {
          const obj: AddOn = {
            id: addOn.id,
            name: addOn.name,
            sumPrice: 0,
            list: [],
            noCharges: false,
            maximumSelect: addOn.maximumSelect,
            defaultSelect: addOn.defaultSelect,
            allowAddMore: addOn.allowAddMore,
          };
          const addOnTypies = addOn
            .types!.split(",")
            .map((type: string) => type.trim());
          let addOnPrices = addOn
            .prices!.split(",")
            .map((type: string) => parseFloat(type.trim()));

          if (addOnPrices.length === 1 && isNaN(addOnPrices[0])) {
            addOnPrices = [];
          }

          // push list
          addOnTypies.forEach((type: string, index: number) => {
            obj.list.push({
              name: type,
              added: 0,
              price: addOnPrices[index] ?? 0,
            } as AddOnItem);
          });

          obj.noCharges = obj.list.every(
            (addOnItem: AddOnItem) => addOnItem.price === 0
          );

          return obj;
        });
        // #endregion

        // #region item
        dataItems = dataItems.map((item: Item) => {
          const obj: Item = {
            id: item.id,
            categoryName: item.categoryName,
            image: item.image,
            code: item.code,
            name: item.name,
            description: item.description,
            note: item.note,
            mode: item.mode,
            addOnList: [],
            prices: [],
            added: 0,
            orderHistoryItemPrices: [],
            orderListItemPrices: [],
          };

          // build prices
          for (const key in item) {
            const value = (item as any)[key];
            if (key.substring(0, 6) === "price_") {
              if (value !== null) {
                obj.prices.push({
                  name: key.substring(6, (key as string).length),
                  added: 0,
                  price: value,
                  sumPrice: 0,
                  itemInfo: item,
                });
              }
            }
          }

          // build addOnList
          if (item.addOn !== "") {
            const addOnNames = (item.addOn as string).split(",");
            addOnNames.forEach((name: string) => {
              const findedAddOn = arrayFind<AddOn>(
                dataAddOnList,
                "name",
                name
              )!;
              // push addOnList
              obj.addOnList.push(findedAddOn);
            });
          }

          return obj;
        });
        // #endregion

        // #region category
        dataCategories = dataCategories.map(
          (category: Category): Category => ({
            id: category.id,
            name: category.name,
            items: dataItems.filter(
              (item: Item) => item.categoryName === category.name
            ),
          })
        );
        // #endregion

        // #region banner
        dataBanners = dataBanners.map(
          (banner: Banner): Banner => ({
            id: banner.id,
            description: banner.description,
            image: banner.image,
            itemCode: banner.itemCode,
            item: dataItems.find((item: Item) => item.code === banner.itemCode),
          })
        );
        // #endregion

        // #region orderList
        let dataOrderList = getOrderListFromLS() ?? [];
        // 做到这里
        // dataOrderList.forEach((itemPrice: Price) => {
        //   const findedItem = dataItems.find(
        //     (item: Item) => item.id === itemPrice.itemInfo.id
        //   )!;
        //   findedItem.added = itemPrice.added;
        //   findedItem.prices.forEach((findedItemPrice: Price) => {
        //     findedItemPrice.added = itemPrice.prices.find(
        //       (orderListItemPrice: Price) =>
        //         orderListItemPrice.name === findedItemPrice.name
        //     )!.added;
        //   });
        //   dataOrderList.push(findedItem);
        // });
        // #endregion

        systemData.value = dataSystem;
        items.value = dataItems;
        categories.value = dataCategories;
        banners.value = dataBanners;
        orderList.value = dataOrderList;
        isLoading.value = false;

        // orderHistory
        const dataOrderHistories: Order[] = []; // temp
        orderHistories.value = dataOrderHistories; // temp
        // if (res[1]) {
        //   res[1].text().then((rep) => {
        //     let dataOrderHistory = jsonToObject(rep)[0];

        //     // 先处理 orderHistory
        //     if (dataOrderHistory) {
        //       const orderHistoryCreatedDate = formatToJSDate(
        //         dataOrderHistory.createdDate
        //       );
        //       const betweenMinute =
        //         (new Date().getTime() - orderHistoryCreatedDate.getTime()) /
        //         1000 /
        //         60;

        //       // auto update status if more than 2 hour
        //       if (betweenMinute >= 120) {
        //         dataOrderHistory.note = `Error! Status updated from "${dataOrderHistory.status}" to "${OrderStatus.cancelled}" due to order over 2 hours!`;
        //         dataOrderHistory.status = OrderStatus.cancelled;
        //         dataOrderHistory.createdDate = formatToGoogleSheetDate(
        //           new Date(formatToJSDate(dataOrderHistory.createdDate))
        //         );

        //         fetchPost<Order>(
        //           CRUD.update,
        //           "Orders",
        //           proxyData.value.fetchCode,
        //           dataOrderHistory
        //         );

        //         dataOrderHistory = null;
        //       } else {
        //         dataOrderHistory.items = JSON.parse(
        //           dataOrderHistory.items as string
        //         ) as Item[];

        //         dataOrderHistory.items.forEach((orderHistoryItem: Item) => {
        //           items.value.find(
        //             (item: Item) => item.id === orderHistoryItem.id
        //           )!.isPrevOrder = true;
        //           orderHistoryItem.added = orderHistoryItem.addedList!.reduce(
        //             (total: number, added: number) => (total += added),
        //             0
        //           );
        //         });
        //       }
        //       orderHistory.value = dataOrderHistory;
        //     }
        //   });
        // }
      });
    });

    return {
      proxyData,
      systemData,
      banners,
      categories,
      orderHistories,
      items,
      orderList,
      customerInfo,
      isLoading,
    };
  },
  data() {
    return {
      diplayItemId: null as null | number,
      isShowOrderListModal: false,
      isShowOrderListModalLoading: false,
      isShowAlertModal: false,
      isShowOrderHistoryModal: false,
      orderNo: 0,
    };
  },
  computed: {
    totalAmount() {
      const orderListTotal = arraySum(this.orderList, "sumPrice");
      const orderHistoryTotal: number =
        this.orderHistories.length > 0
          ? arraySum(this.orderHistories[0].itemPrices as Price[], "sumPrice")
          : 0;
      return orderListTotal + orderHistoryTotal;
    },
  },
  methods: {
    showItemDetail(id: number): void {
      this.diplayItemId = id;
    },
    closeItemDetail() {
      this.diplayItemId = null;
    },
    updateItemAndItemPrice(item: Item) {
      item.added = arraySum(item.prices, "added");
      item.prices.forEach((itemPrice: Price) => {
        itemPrice.sumPrice = itemPrice.price * itemPrice.added;
      });
    },
    updateItemPriceToOrder(value: { itemPrice: Price; qty: number }) {
      const findedItem = arrayFind<Item>(
        this.items,
        "id",
        value.itemPrice.itemInfo.id
      )!;
      const findedItemPrice = arrayFind<Price>(
        findedItem.prices,
        "name",
        value.itemPrice.name
      )!;

      // 更新数量
      findedItemPrice.added =
        value.qty === -2 ? 0 : findedItemPrice.added + value.qty;
      if (findedItemPrice.added === 0) {
        // 删除
        this.orderList.forEach((itemPrice: Price, index: number) => {
          if (
            itemPrice.itemInfo.id === itemPrice.itemInfo.id &&
            itemPrice.name === itemPrice.name
          ) {
            this.orderList.splice(index, 1);
          }
        });
      } else {
        // 找不到就添加
        const foundInOrderList = this.orderList.find(
          (itemPrice: Price) =>
            itemPrice.itemInfo.id === value.itemPrice.itemInfo.id &&
            itemPrice.name === value.itemPrice.name
        );
        if (!foundInOrderList) this.orderList.unshift(findedItemPrice);
      }

      this.updateItemAndItemPrice(findedItem);

      // 更新 LS, 不管有没有 orderList 都更新，watch 会接着处理
      setOrderListToLS(this.orderList);
    },
    updateAddOnToOrder(value: { itemPrice: Price; addOn: AddOn; qty: number }) {
      const findedItem = arrayFind<Item>(
        this.items,
        "id",
        value.itemPrice.itemInfo.id
      )!;
      const findedItemPrice = arrayFind<Price>(
        findedItem.prices,
        "name",
        value.itemPrice.name
      )!;

      // 更新数量
      findedItemPrice.added =
        value.qty === -2 ? 0 : findedItemPrice.added + value.qty;
      if (findedItemPrice.added === 0) {
        // 删除
        this.orderList.forEach((itemPrice: Price, index: number) => {
          if (
            itemPrice.itemInfo.id === itemPrice.itemInfo.id &&
            itemPrice.name === itemPrice.name
          ) {
            this.orderList.splice(index, 1);
          }
        });
      } else {
        // 找不到就添加
        const foundInOrderList = this.orderList.find(
          (itemPrice: Price) =>
            itemPrice.itemInfo.id === value.itemPrice.itemInfo.id &&
            itemPrice.name === value.itemPrice.name
        );
        if (!foundInOrderList) this.orderList.unshift(findedItemPrice);
      }

      this.updateItemAndItemPrice(findedItem);

      // 更新 LS, 不管有没有 orderList 都更新，watch 会接着处理
      setOrderListToLS(this.orderList);
    },
    showOrderListModal(value: boolean) {
      this.isShowOrderListModal = value;
    },
    removeOrderListItem(id: number) {
      const findedItem = arrayFind<Item>(this.items, "id", id)!;

      // 更新数量
      findedItem.prices.forEach(
        (findedItemPrice: Price) => (findedItemPrice.added = 0)
      );
      this.updateItemAndItemPrice(findedItem);

      // 删除多个 itemPrice
      this.orderList.reduceRight((_, itemPrice: Price, index: number) => {
        if (itemPrice.itemInfo.id === id) this.orderList.splice(index, 1);
        return _;
      }, null);

      // 更新 LS, 不管有没有 orderList 都更新，watch 会接着处理
      setOrderListToLS(this.orderList);
    },
    removeOrderListItemPrice(itemPrice: Price) {
      this.updateItemPriceToOrder({ itemPrice, qty: -2 });
    },
    orderSubmitted(value: { order: Order; postOrUpdate: PostOrUpdate }) {
      this.isShowOrderListModalLoading = true;
      value.order.deviceId = this.customerInfo!.deviceId;

      if (value.postOrUpdate === PostOrUpdate.post) {
        // post order
        fetchPostByType("createOrder", this.proxyData.fetchCode, value.order)
          .then(() => {
            removeOrderListFromLS();

            // get new order Id
            fetch(
              `${this.proxyData.orderUrl}sheet=Orders&tq=${encodeURIComponent(
                "select * where K = "
              )}${this.customerInfo!.deviceId} and C = ${encodeURIComponent(
                `"${OrderStatus.waiting}"`
              )}`
            )
              .then((res) => res.text())
              .then((rep) => {
                this.isShowOrderListModalLoading = false;
                this.orderNo = jsonToObject(rep)[0].orderNo!;

                setTimeout(() => {
                  this.isShowOrderListModal = false;
                  this.orderList = [];
                  this.items.forEach((item: Item) => (item.added = 0));
                }, 1000);
                setTimeout(() => {
                  this.isShowAlertModal = true;
                }, 2000);
              });
          })
          .catch(() => {
            alert(
              "Error! Submit fail, please inform waiters and order manually!"
            );
          });
      } else {
        // update order
        fetchPost<Order>(
          CRUD.update,
          "Orders",
          this.proxyData.fetchCode,
          value.order
        )
          .then(() => {
            removeOrderListFromLS();
            console.log("这里逻辑不对, orderNo 为什么可以拿到？");
            this.orderNo = this.orderHistories[0]!.orderNo!;
            setTimeout(() => {
              this.isShowOrderListModal = false;
              this.orderList = [];
              this.items.forEach((item: Item) => (item.added = 0));
            }, 1000);
            setTimeout(() => {
              this.isShowAlertModal = true;
            }, 2000);
          })
          .catch(() => {
            alert(
              "Error! Submit fail, please inform waiters and order manually!"
            );
          });
      }
    },
    showOrderListModalLoading(value: boolean) {
      this.isShowOrderListModalLoading = value;
    },
    showAlertModal(value: boolean) {
      this.isShowAlertModal = value;
    },
    showOrderHistoryModal(value: boolean) {
      this.isShowOrderHistoryModal = value;
    },
  },
  watch: {
    orderHistory(newValue) {
      if (newValue !== null) {
        setTimeout(() => {
          this.isShowOrderHistoryModal = true;
        }, 1000);
      }
    },
    orderList(newValues, oldValues) {
      if (newValues.length !== oldValues.length && newValues.length === 0) {
        removeOrderListFromLS();
      }
    },
    // items: {
    //   handler(items) {
    //     items.forEach((item: Item) => {
    //       // 只负责更新 item.added & itemPrice.sumPrice
    //       item.added = arraySum(item.prices, "added");

    //       item.prices.forEach((itemPrice: Price) => {
    //         itemPrice.sumPrice = itemPrice.price * itemPrice.added;

    //         if (item.added === 0) {
    //           console.log(0);
    //           let indexOf = this.orderList.findIndex(
    //             (itemPrice: Price) =>
    //               itemPrice.itemInfo.id === item.id &&
    //               itemPrice.name === itemPrice.name
    //           );
    //           this.orderList.splice(indexOf, 1);
    //         }
    //       });
    //     });
    //   },
    //   deep: true,
    // },
  },
});
