
import { ImageSize, Item, Order, Price, System } from "@/interface";
import {
  arraySum,
  convertFlickrImage,
  padStart3,
  rounding,
} from "@/mixins/generalMixin";
import { defineComponent, PropType, ref } from "vue";

export default defineComponent({
  props: {
    isShowOrderHistoryModal: {
      type: Boolean,
      required: true,
    },
    orderHistory: {
      type: Object as PropType<Order>,
      required: true,
    },
    systemData: {
      type: Object as PropType<System>,
      required: true,
    },
  },
  emits: ["showOrderHistoryModal"],
  data() {
    return {
      ImageSize: ImageSize,
    };
  },
  computed: {
    itemPrices() {
      return this.orderHistory.itemPrices as Price[];
    },
    orderNo() {
      return padStart3(this.orderHistory.orderNo as number);
    },
    totalAmount() {
      return arraySum(this.orderHistory.itemPrices as Price[], "sumPrice");
    },
    serviceCharge() {
      return this.systemData.serviceCharge10Percent
        ? this.totalAmount * 0.1
        : 0;
    },
    tax() {
      return this.systemData.tax6Percent ? this.totalAmount * 0.06 : 0;
    },
    roundingAdj() {
      const serviceCharge = parseFloat(this.serviceCharge.toFixed(2));
      const tax = parseFloat(this.tax.toFixed(2));
      return rounding(this.totalAmount + serviceCharge + tax);
    },
    nettTotal() {
      return (
        this.totalAmount + this.serviceCharge + this.tax + this.roundingAdj
      );
    },
  },
  methods: {
    showOrderHistoryModal(value: boolean) {
      this.$emit("showOrderHistoryModal", value);
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
});
