import { ImageSize } from "@/interface";

export function rounding(value: number) {
    const total = value.toFixed(2);
    const cen = parseInt(total.substring(total.length - 1, total.length));
    if (cen === 0) {
        return 0;
    } else if (cen <= 4) {
        return (cen / 100) * -1;
    } else {
        return (10 - cen) / 100;
    }
}

export function padStart3(val: number) {
    return val.toString().padStart(3, "0");
}

export function convertGoogleDriveImage(imageLink: string) {
    const isGoogleDriveLink = imageLink.includes('drive.google.com');
    if (isGoogleDriveLink) {
        const splits = imageLink.split('/');
        return `https://lh3.google.com/u/0/d/${splits[5]}`
    }
    else {
        return imageLink;
    }
}

export function convertFlickrImage(imageLink: string, size: ImageSize | null) {
    const isFlickrLink = imageLink.includes('staticflickr.com');
    if (isFlickrLink) {
        return size ? imageLink.replace(/_[a-z].jpg/, `_${size}.jpg`) : imageLink;
    }
    else {
        alert('Image is not from staticflickr.com')
        return '';
    }
}

export function arraySum(arr: any[], key: string) {
    return arr.reduce((total: number, obj: any) => total += obj[key], 0)
}

export function arrayMin<T>(arr: T[], key: keyof T) {
    const results = arr.map((obj: T) => +obj[key]);
    return Math.min(...results);
}

export function arrayFind<T>(arr: T[], key: keyof T, value: any) {
    return arr.find((obj: T) => obj[key] === value)
}

