import { Sheet } from "@/interface";

export function jsonToObject(rep: any) {
    const data = JSON.parse(rep.substring(47).slice(0, -2));
    const cols = data.table.cols;

    if (data.table.parsedNumHeaders === 0) {
        return [];
    }

    return data.table.rows.map((row: any) => {
        const obj = {} as any;
        row.c.forEach((data: any, index: number) => {
            // 下划线是私有，目前用在 Google Sheet 的显示图片
            if (cols[index].label.substring(0, 1) === "_") {
                return
            }
            // data === null 是因为 Google Sheet 的来源是空
            // 如果数字是空的话，就一定要用 0 ，这是为了作为区分
            obj[cols[index].label] = data === null ? '' : data.v;
        });

        return obj;
    });
}

export function allSheetJsonToObject(rep: any) {
    const allDatas = JSON.parse(rep.substring(47).slice(0, -2));
    const rows = allDatas.table.rows;
    const sheets: Sheet[] = [];

    if (allDatas.table.parsedNumHeaders === 1) {
        alert('Please inform programmer! Error Code: 01')
    }
    // set sheet name
    rows[0].c.forEach((row: any) => {
        sheets.push({
            name: row.v,
            datas: []
        });
    });
    // set sheet data
    rows[1].c.forEach((row: any, cellIndex: number) => {
        let keys: string[] = [];
        JSON.parse(row.v).forEach((JSONRow: any, JSONRowIndex: number) => {
            if (JSONRowIndex === 0) {
                keys = JSONRow;
            }
            else {
                const obj = {} as any;
                JSONRow.forEach((JSONCell: any, JSONCellIndex: number) => {
                    obj[keys[JSONCellIndex]] = JSONCell;
                });
                sheets[cellIndex].datas.push(obj);
            }
        });
    });
    return sheets
}
