
import { Item, ItemMode, Price } from "@/interface";
import { arrayMin } from "@/mixins/generalMixin";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    item: {
      type: Object as PropType<Item>,
      required: true,
    },
    fontSize: {
      type: Number,
    },
  },
  emits: ["updateItemPriceToOrder", "showItemDetail"],
  data() {
    return {
      itemMode: ItemMode,
    };
  },
  computed: {
    fontSizeStyle() {
      return this.fontSize ? { fontSize: "16px" } : undefined;
    },
  },
  methods: {
    updateItemPriceToOrder(itemPrice: Price, qty: number) {
      this.$emit("updateItemPriceToOrder", { itemPrice, qty });
    },
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    getMinPrice(itemPrices: Price[]) {
      return arrayMin(itemPrices, "price");
    },
  },
});
