
import { AddOn, ImageSize, Item, ItemMode, Price } from "@/interface";
import { arrayFind, convertFlickrImage } from "@/mixins/generalMixin";
import { defineComponent } from "vue";

interface SelectedPrice {
  name: string;
  added: number;
}

export default defineComponent({
  props: ["diplayItemId", "items", "totalAmount", "orderList"],
  emits: [
    "closeItemDetail",
    "updateItemPriceToOrder",
    "updateAddOnToOrder",
    "removeOrderListItemPrice",
  ],
  data() {
    return {
      item: {} as Item,
      ImageSize: ImageSize,
      itemMode: ItemMode,
      selectedItemPrice: undefined as SelectedPrice | undefined,
    };
  },
  computed: {
    totalAdded() {
      return this.item.prices.reduce(
        (total, price: Price) => total + price.added,
        0
      );
    },
    submitText() {
      if (this.item.mode !== ItemMode.addOption) {
        return this.totalAdded > 0 ? `Add (${this.totalAdded})` : "Close";
      } else {
        return "Add";
      }
    },
  },
  methods: {
    updateItemPrice(itemPrice: Price, qty: number) {
      let finalQty = qty;
      if (this.selectedItemPrice !== undefined) {
        if (this.selectedItemPrice.name === itemPrice.name) {
          finalQty = this.selectedItemPrice.added++;
        } else {
          if (this.selectedItemPrice.name !== "") {
            const findedItemPrice = arrayFind<Price>(
              this.item.prices,
              "name",
              this.selectedItemPrice.name
            );
            this.$emit("removeOrderListItemPrice", findedItemPrice);
          }
          this.selectedItemPrice = { name: itemPrice.name, added: qty };
        }
      }
      this.$emit("updateItemPriceToOrder", { itemPrice, qty: finalQty });
    },
    removeOrderListItemPrice(itemPrice: Price) {
      this.$emit("removeOrderListItemPrice", itemPrice);
    },
    updateAddOnToOrder(itemPrice: Price, addOn: AddOn, qty: number) {
      this.$emit("updateAddOnToOrder", { itemPrice, addOn, qty });
    },
    submit() {
      if (this.item.mode !== ItemMode.addOption) {
        this.$emit("closeItemDetail");
      }
    },
    close() {
      this.selectedItemPrice = undefined;
      this.$emit("closeItemDetail");
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
  watch: {
    diplayItemId(newVal) {
      if (newVal !== null) {
        this.item = arrayFind<Item>(this.items, "id", newVal)!;
        this.selectedItemPrice =
          this.item.mode === ItemMode.addOption
            ? { name: "", added: 0 }
            : undefined;
      }
    },
  },
});
